<template>
    <div id="header">
        <div class="inner">
            <div class="homeimg"><a href="/"><img src="/images/home.png"></a></div>
            <div class="slogan">{{ name }}</div>
            <div class="slogan2">{{ address }}</div>
            <div class="icon"></div>
        </div>
    </div>
</template>

<script>
    module.exports = {
        data: function () {
            return {
                name: '      ',
                address: '. , 24 .(8482)22-24-74'
            }
        }
    }
</script>

<style lang="scss">
    #header {
        background: #65b60d url(/images/bg_header.jpg) repeat-x;
        height: 150px;
        min-width: 1000px;
        .inner {
            background: url(/images/bg_header_2.jpg) no-repeat right bottom;
            height: 145px;
        }
        .slogan {
            color: #ffffff;
            font-size: 26px;
            font-weight:bold;
            left: 52%;
            margin: 0 0 0 -375px;
            position:absolute;
            text-align:center;
            top: 73px;
            width: 686px;
        }
        .slogan2 {
            color: #ffffff;
            font-size: 16px;
            left: 73%;
            position:absolute;
            top: 58px;
        }
        .icon {
            background: url(/images/icon.png);
            height: 104px;
            left: 50px;
            position:absolute;
            top: 0px;
            width: 53px;
        }
        .homeimg {
            top: 120px;
            position:absolute;
            left: 65px;
        }
    }
</style>



