import Vue from 'vue';
import header from './components/HeaderBlock.vue';
import navigation from './components/Navigation.vue';
import footer from './components/FooterBlock.vue';
import slider from './components/Slider.vue';
import namaz from './components/Namaz.vue';
import gallery from './components/Gallery.vue';
import hadis from './components/Hadis.vue';
import ayat from './components/Ayat.vue';

import './assets/styles/main.scss';

new Vue({
    el: '#app',
    components: {
        'headerblock': header,
        'navblock': navigation,
        'footerblock': footer,
        'sliderblock': slider,
        'namaz': namaz,
        'hadis': hadis,
        'ayat': ayat,
        'e-gallery': gallery
    }
});


