<template>
    <div v-if="times && times.length" class="namaz--container">
        <h1> </h1>
        <button :click="changePeriod"></button>
        <div v-for="day in times" v-if="day.data.length" v-show="day.period === period" class="namaz--day">
            <h2>{{ day.period }}</h2>
            <div v-for="time in day.data"  class="namaz--row">
                <div class="namaz--name">{{time.name}}</div>
                <div class="namaz--time">{{time.value}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        data: function () {
            return {
                times: [],
                errors: [],
                period: ''
            }
        },
        created: function () {
            axios.get('api/get-daily-namaz-times')
                .then(response => {
                    this.times = response.data.namaz;
                })
                .catch(e => {
                    this.errors.push(e);
                })
        },
        computed: function () {

        },
        methods: {
            changePeriod: function () {
                if (this.period === '') {
                    this.period = '';
                } else {
                    this.period = '';
                }

            }
        }
    }
</script>

<style lang="scss">
    .namaz--container {
        padding: 10px;
        h1 {
            margin: 0;
            padding: 20px 0px;
            text-align: center;
            font-size: 30px;
        }
        .namaz--day {
            text-align: center;
            h2 {
                font-size: 20px;
                padding: 0px 0 10px 0;
            }

            .namaz--row {
                padding: 20px;
                border: 1px solid #3d893d;
                font-size: 18px;
                text-align: center;

                .namaz--name {
                    width: 50%;
                    float: left;
                }
                .namaz--time {

                }
            }
        }
    }
</style>


